import React from "react"
import tempologo from "./images/tempologo.svg"
import search from "./images/search.svg"
import globe from "./images/globe.svg"
export default function Navbar() {
  return (
    <div className="hbar">

      <img className="tempologo" src={tempologo} width="10.5%" height="100%" />
      <div className="nav">
        <span className="hbar_solutions">Solutions</span>
        <span className="hbar_resources">Resources</span>
        <span className="hbar_support">Support</span>
        <span className="hbar_aboutus">About Us</span>
        <span className="hbar_pricing">Pricing</span>
      </div>
      <div className="hbar_icons">
        <img className="search" src={search} />
        <div className="bd">
          <img className="globe" src={globe} />
        </div>
      </div>
      <button className="hbar_btn">Get started</button>

      <div className="min_icons">
        <img className="searc" src={search} />
        <div className="mobile-trigger">
          <div className="tr-line" id="x"></div>
          <div className="tr-line" id="y"></div>
          <div className="tr-line" id="x"></div>
        </div>
      </div>

    </div>
  )
}