import React from "react"

export default function Announcements() {
  const greater = ">"
  return (
    <div className="bar2">

      <div className="a">ANNOUNCEMENTS</div>
      <div className="b">
        Tempo acquires ALM Works to deliver the first purpose-built program and portfolio
        management suite of apps to the Atlassian Marketplace.

        <div className="ba"> {greater} </div>
      </div>

    </div>
  )
}