import React from "react"
import timesheet from "./images/timesheets.svg"
import planner from "./images/planner.svg"
import budgets from "./images/budgets.svg"
import tracker from "./images/tracker.svg"
import prime from "./images/prime.svg"
import heroshot from "./images/heroshot.svg"
import timesheeticon from "./images/timesheet-icon.svg"
import plannericon from "./images/planner-icon.svg"
import budgeticon from "./images/budgets-icon.svg"
import jira from "./images/jirascreen.webp"

export default function Cards(){
  return  (
    <div className="bod3">
      <div className="pdiv">
        <div className="para6">Our time tracking and management product suite</div>
      </div>
      <div className="bod4">
        <div className="bod4_1">
          <div className="tfj">TEMPO FOR JIRA</div>
          <div className="attj">Automated Time Tracking in Jira</div>
          <div className="temponote">Tempo offers insightful data that helps teams better manage their time and
            finances.</div>
        </div>
        <img className="jira" src={jira}/>
      </div>

      <div className="cards">
        <div className="card" id="ts">
          <div className="cardimage" style={{ backgroundImage: `url(${timesheet})` }}></div>
          <div className="cardtext">
            <div className="bname">
              <img src={timesheeticon}/>
                <div className="ncard" id="tsheet">TEMPO TIMESHEETS</div>
            </div>
            <div className="carddesc">
              Time tracking made easy
            </div>
          </div>
        </div>
        <div className="card" id="ts">
          <div className="cardimage" style={{backgroundImage: `url(${planner})`}}></div>
          <div className="cardtext">
            <div className="bname">
              <img src={plannericon}/>
                <div className="ncard" id="planner"> TEMPO PLANNER </div>
            </div>
            <div className="carddesc">
              Optimize your workforce effortlessly
            </div>
          </div>
        </div>
        <div className="card" id="ts">
          <div className="cardimage" style={{backgroundImage: `url(${budgets})`}}></div>
          <div className="cardtext">
            <div className="bname">
              <img src={budgeticon}/>
                <div className="ncard" id="budgets">TEMPO BUDGETS</div>
            </div>
            <div className="carddesc">
              Extensive financial tracking in Jira
            </div>
          </div>

        </div>
        <div className="card" id="ct1">
          <div className="cardimage" style={{backgroundImage: `url(${tracker})`}}></div>
          <div className="cardtext">
            <div className="bname">
              <img src={timesheeticon}/>
                <div className="ncard" id="budgets">COST TRACKER</div>
            </div>
            <div className="carddesc">
              Simple cost tracking for Timesheets Cloud
            </div>
          </div>
        </div>
        <div className="card" id="ct">
          <div className="cardimage" style={{backgroundImage: `url(${prime})`}}></div>
          <div className="cardtext" id="tr">
            <div className="bname">
              <img src={plannericon}/>
                <div className="ncard" id="prime"> TIMESHEET REPORTS AND GADGETS </div>
            </div>
            <div className="carddesc" id="lw">
              Lightweight Time Reporting for Jira
            </div>
          </div>
        </div>
        <div className="card" id="ct">
          <div className="cardimage" id="tsuitei" style={{backgroundImage: `url(${heroshot})`}}></div>
          <div className="cardtext">
            <div className="bname">
              <img src={budgeticon}/>
                <div className="ncard" id="tsuite">TEMPO SUITE</div>
            </div>
            <div className="carddesc">
              Seamless migration from Server to Cloud
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}