import React from "react"
import image1 from './images/boeing.webp'
import image2 from './images/vw.png'
import image3 from './images/oracle.webp'
import image4 from './images/office-depot.webp'

export default function DisplayIcons() {
  return (
    <div className="displayicons">
      <img className="imgd" id="od" src={image1} />
      <img className="imgd" id="vw" src={image2} />
      <img className="imgd" src={image3} />
      <img className="imgd" id="dimg" src={image4} />
    </div>
  )
}