import React from "react"
import bg from "./images/red.webp"
import redlogo from "./images/redlogo.webp"

export default function TempoNote(){
  const greater = ">"
  return(
    <div className="heh">
      <div className="bg" style={{ backgroundImage: `url(${bg})` }}>
        <img className="red" src={redlogo}/>
      </div>
      <div className="box2">
        <div className="para3">
          "Since implementing the Tempo
          suite, the REDspace team has estimated cost savings of over
          $220,000 CDN or $25,000 CDN per Project Manager annually."
        </div>
        <div className="para4">Angela Campbell | REDspace</div>
        <div className="note">
          <div className="row">
            <div className="para5">Read the full story</div>
            <div className="gts">{greater}</div>
          </div>
          <button className="btn2">More success stories</button>
        </div>
      </div>
    </div>
  )
}